<template>
	<view style="flex-flow: column nowrap;width: 750upx; position: relative;">

		<image class="bac_image" src="http://kaikaiomg-kaikaiomg.stor.sinaapp.com/uni_app_image/card_pic/kaiji.jpg" :style="'height:'+windowHeight+'px'"></image>


		<view v-if="ifallowgotohot" class="" style="position: absolute;top: 750upx; justify-content: center;align-items: center; width: 750upx;">
			<!-- #ifdef APP-PLUS -->
			<button type="primary" @tap="appgetUserInfo" class="button_style">获取用户信息</button>
			<!-- #endif -->
			<!-- #ifdef MP-WEIXIN -->
			<button type="primary" open-type="getUserInfo" @getuserinfo="wxGetUserInfo" class="button_style">进入齿轮城</button>
			<!-- #endif -->

		</view>
		<view v-if="!ifallowgotohot" class="" style="flex-flow: column;  position: absolute;top: 750upx; justify-content: center;align-items: center; width: 750upx;">
			<image src="../../static/loading3.gif" style="width: 300upx;height: 200upx; opacity:0.9;"></image>
			<progress class="progress_style" :percent="finish_task" active-mode="forwards" active show-info stroke-width="10"
			 activeColor="#10AEFF" backgroundColor="blue" />
		</view>




	</view>

</template>

<script>
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				fileSrc: "",
				savedFilePath: "",
				// jsondata: null
				ifallowgotohot: false,
				windowHeight: null,
				finish_task: 0,


			}
		},
		onLoad: async function(option) {
			var that = this;
			that.windowHeight = uni.getSystemInfoSync().windowHeight,
			await uni.getStorage({
				key: 'allcitys_map_json',
				success: function(res) {
					console.log("find_json nodownload", res.data);
					that.finish_task += 30;
				},
				fail: function() {
					that.download_json('https://kaikaiomg.applinzi.com/material/echarts-mapJson-master/china.json',
						'allcitys_map_json');
					// let getdataresult0 = await that.download_json_post('https://kaikaiomg.applinzi.com/material/echarts-mapJson-master/china.json','allcitys_map_json');
					that.finish_task += 30;
				}
			});
			await uni.getStorage({
				key: 'phonenumber',
				success: function(res) {
					console.log("find_phone_number", res.data);
					that.finish_task += 30;
					that.setphonenum(res.data);
				},
				fail: function() {
					console.log("havent_find_phone_number");
					that.finish_task += 30;
				}
			});
			
			
			let getdataresult1 = await that.download_json_post('https://kaikaiomg.applinzi.com/uni_app_download.php','hot_card_list');
			that.finish_task += 20;
			let getdataresult2 = await that.download_json_post('https://kaikaiomg.applinzi.com/uni_app_download.php','uni_app_hot_list_ziduan');
			that.finish_task += 20;
// 			let getdataresult3 = await that.download_json_post('https://kaikaiomg.applinzi.com/uni_app_download.php','get_collect_list');
// 			that.finish_task += 20;

			setTimeout(function() {
				if (that.finish_task == 100) {
					that.ifallowgotohot = true;
				}
			}, 2000)
			setTimeout(function() {
				if (that.finish_task == 100) {
					that.ifallowgotohot = true;
				}
			}, 3000)
			setTimeout(function() {
				if (that.finish_task == 100) {
					that.ifallowgotohot = true;
				}
			}, 4000)


		},
		computed: {
			...mapState(['hasLogin', 'userInfo', 'phonenum', 'openid'])
		},
		methods: {
			...mapMutations(['login', 'logout', 'setopenid', 'setluyouData','setphonenum']),
			wxGetUserInfo: function(e) {

				var that = this;
				console.log(e.detail.userInfo)
				if (e.detail.userInfo) {
					// 发送 res.code 到后台换取 openId, sessionKey, unionId
					uni.login({
						provider: 'weixin',
						success: function(loginRes) {
							// that.get_access_token(loginRes.code);

							console.log("code:", loginRes.code);
							that.upload_oauthData(e.detail.userInfo, loginRes.code)
							that.login(e.detail.userInfo, loginRes.code)
							uni.reLaunch({
								url: "../main_home/main_home"
							})
						}

					})
				} else {
					console.log(333, '执行到这里，说明拒绝了授权')
					uni.showToast({
						title: "为了您更好的体验,请先同意授权",
						icon: 'none',
						duration: 2000,

					});
				}

			},


			appgetUserInfo: function() {
				var that = this;
				uni.login({
					provider: 'weixin',
					success: (loginRes) => {
						console.log("code:", loginRes.code);
						uni.getUserInfo({
							provider: 'weixin',
							success: (e) => {
								console.log("得到用户信息", e);

								that.upload_oauthData(e.userInfo, loginRes.code)
								that.login(e.userInfo, loginRes.code)
								uni.reLaunch({
									url: "../main_echart/main_echart"
								})
							},
							fail: (e) => {
								console.log("fail", e);
								let content = e.errMsg;
								if (~content.indexOf("uni.login")) {
									content = "请在登录页面完成登录操作"
								}
								uni.showModal({
									title: "获取用户信息失败",
									content: "错误原因" + content,
									showCancel: false
								})
							}
						})


					},
					fail: (e) => {
						console.log("fail", e);
					}
				})



			},
			upload_oauthData(userInfo, code) {
				var that = this;
				uni.request({
					url: 'https://kaikaiomg.applinzi.com/uni_app_upload.php',
					method: "POST",
					data: {
						'form': 'oauthData',
						'nickName': userInfo.nickName,
						'avatarUrl': userInfo.avatarUrl,
						'city': userInfo.city,
						'country': userInfo.country,
						'gender': userInfo.gender,
						'language': userInfo.language,
						'province': userInfo.province,
						'code': code,
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (ret) => {
						if (ret.statusCode !== 200) {
							console.log("请求失败:", ret)
						} else {
							console.log("success_uodate_oauthData:", ret.data)
							console.log("success_uodate_oauthData:", ret.data.openid)
							that.setopenid(ret.data.openid)
						}
					},
				})
			},

			download_json: function(urlpath, keytitle) {
				var that = this;
				uni.request({
					url: urlpath,
					method: "GET",

					success: (ret) => {
						if (ret.statusCode !== 200) {
							console.log("请求失败:", ret)
							that.ifallowgotohot = true;
						} else {
							console.log("download_json:", ret.data)
							// that.jsondata = ret.data.data;

							uni.setStorage({
								key: keytitle,
								data: ret.data,
								success: function() {
									console.log('getdata_success:' + keytitle);
								}
							});


						}
					},
				})
			},
			download_json_post: function(urlpath, keytitle) {
				var that = this;
				return new Promise((res) => {
					uni.request({
						url: urlpath,
						method: "POST",
						data: {
							'form': keytitle,
						},
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						},

						success: (ret) => {
							if (ret.statusCode !== 200) {
								console.log("请求失败:", ret)
								that.ifallowgotohot = true;
							} else {
								console.log(keytitle+":", ret.data)
								// that.jsondata = ret.data.data;
								// that.setluyouData(ret.data.data)
								uni.setStorage({
									key: keytitle,
									data: ret.data,
									success: function() {
										// console.log(keytitle+':' + keytitle);
									}
								});


							}
						},
					})
					res('ss');
				})

			},

			download_file: function() {
				var that = this;
				uni.downloadFile({
					url: "https://kaikaiomg.applinzi.com/material/echart/echartsall.js",
					success: (res) => {
						console.log('downloadFile success, res is', res)
						that.fileSrc = res.tempFilePath;




						uni.saveFile({
							tempFilePath: that.fileSrc,
							success: function(res2) {
								that.savedFilePath = res2.savedFilePath;
								// console.log("savedFilePath", that.savedFilePath);
								uni.setStorage({
									key: 'echart_filepath',
									data: that.savedFilePath,
									success: function() {
										console.log('filepath:' + that.savedFilePath);
									}
								});


							},
						});


						// 
					},
					fail: (err) => {
						console.log('downloadFile fail, err is:', err)
					}
				});

			},

			get_access_token(code) {
				uni.request({
					url: 'https://api.weixin.qq.com/sns/jscode2session',
					method: "POST",
					data: {
						'code': code,
						'secret': 'af71a56b3018a06361dac58358963a76',
						'appid': 'wx7956f7b5acf09c52',
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},

					success: (ret) => {
						console.log("get_access_token:", ret)
						// that.jsondata = ret.data.data;
					},
				})


			},



		}
	}
</script>

<style>
	.bac_image {
		width: 750upx;

	}

	.progress_style {
		z-index: 20;
		height: 50upx;
		width: 400upx;
		padding-left: 33upx;
	}

	.button_style {

		border-radius: 12upx;
		background-color: #71C671;
		width: 220upx;
		height: 60upx;
		line-height: 60upx;
		font-weight: bold;
		font-size: 30upx;
		text-align: center;

	}
</style>



